export const normalizeString = (name) => {
  let result = name;
  result = result.replace("", "");
  result = result.replace("", "");
  result = result.replace("", "");
  result = result.replace("", "");
  result = result.replace(" ", " ");
  result = result.replace("", "");
  result = result.replace("", "");
  result = result.replace("", "");
  result = result.replace(" ", "");

  return result;
};
