import React, { useCallback, useEffect } from "react";
import FileUploadButton from "../../../../components/fileUploadButton";
import Papa from "papaparse";
import { Alert, Box, Divider, Stack } from "@mui/material";
import { useAuth0 } from "@auth0/auth0-react";
import { createStudents } from "../../../../services/students";
import ImportSample from "./importSample";

const cleanPhoneNumber = (phoneNumber) => {
  return `${phoneNumber}`.replace(
    /[\s\u0000-\u001F\u007F-\u00A0\u200B-\u200D\u200E-\u206F]/g,
    ""
  );
};

const formatPhoneNumber = (phoneNumber) => {
  // for scinetific notation
  const hasNonDigits = `${phoneNumber}`
    .split("")
    .find((letter) => isNaN(Number(letter)));

  return hasNonDigits ? `${Number(phoneNumber)}` : `${phoneNumber}`;
};

export default function ImportStudents() {
  const [error, setError] = React.useState(false);
  const [validationErrorMessages, setValidationErrorMessages] = React.useState(
    []
  );

  const [loading, setLoading] = React.useState(false);
  const [file, setFile] = React.useState(null);
  const [submittedSuccessfully, setSubmittedSuccessfully] =
    React.useState(false);
  const fileInput = React.useRef(null);

  const { getAccessTokenSilently } = useAuth0();

  const validateData = useCallback((data) => {
    const errors = [];
    data.forEach((row, index) => {
      if (!row[" /"]) {
        errors.push(`/  ${index + 2}    `);
      }

      if (!row[""]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }    `
        );
      } else if (row[""] !== "" && row[""] !== "") {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }     -      `
        );
      }

      if (!row[" /"]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }     /`
        );
      }

      if (!row[""]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }    `
        );
      }

      if (!row[""]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }    `
        );
      } else if (isNaN(Number(row[""]))) {
        errors.push(
          `  ${row[" /"]}   ${index + 2}  `
        );
      }

      if (!row[" "]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }     `
        );
      } else {
        const year = Number(row[" "]);
        if (isNaN(year)) {
          errors.push(
            `  / ${row[" /"]}   ${
              index + 2
            }  `
          );
        } else if (year < 2020) {
          errors.push(
            `/ ${row[" /"]}   ${
              index + 2
            }    `
          );
        }
      }

      if (!row[" "]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }     `
        );
      } else {
        const semester = Number(row[" "]);
        if (isNaN(semester)) {
          errors.push(
            `  / ${row[" /"]}   ${
              index + 2
            }  `
          );
        } else if (semester < 1 || semester > 3) {
          errors.push(
            `/ ${row[" /"]}   ${
              index + 2
            }      -    1  2  3`
          );
        }
      }

      if (!row[" "]) {
        errors.push(
          `/ ${row[" /"]}   ${
            index + 2
          }     `
        );
      } else {
        const month = Number(row[" "]);
        if (isNaN(month)) {
          errors.push(
            `  / ${row[" /"]}   ${
              index + 2
            }  `
          );
        } else {
          const maxMonth = Number(row[" "]) === 3 ? 1 : 3;
          const hint =
            Number(row[" "]) === 3
              ? "    "
              : " 3   ";

          if (month < 1 || month > maxMonth) {
            errors.push(
              `/ ${row[" /"]}   ${
                index + 2
              }      - ${hint}`
            );
          }
        }
      }

      if (row[" "]) {
        let phoneNumber = cleanPhoneNumber(row[" "]);
        if (isNaN(Number(phoneNumber))) {
          errors.push(
            `  / ${phoneNumber}   ${index + 2}  `
          );
        } else {
          phoneNumber = formatPhoneNumber(phoneNumber);

          if (
            `${phoneNumber}`.length !== 10 &&
            `${phoneNumber}`.length !== 12 &&
            `${phoneNumber}`.length !== 14
          ) {
            errors.push(
              `  / ${row[" /"]}   ${
                index + 2
              }    7   12  14  (  )`
            );
          }
        }
      }
    });

    const namesCount = data.reduce((acc, row) => {
      if (!row[" /"]?.trim()) return acc;

      return {
        ...acc,
        [row[" /"]]: (acc[row[" /"]] || 0) + 1,
      };
    }, {});

    Object.entries(namesCount).forEach(([row, count]) => {
      if (count > 1) {
        errors.push(`/ ${row}   `);
      }
    });

    const phoneNumbersCount = data.reduce((acc, row) => {
      console.log(row);
      if (!`${row[" "] ?? ""}`?.trim()) return acc;

      return {
        ...acc,
        [row[" "]]: (acc[row[" "]] || 0) + 1,
      };
    }, {});

    Object.entries(phoneNumbersCount).forEach(([phoneNumber, count]) => {
      if (count > 1) {
        errors.push(`  ${phoneNumber}   `);
      }
    }, {});

    return errors;
  }, []);

  const mapRowsToStudents = useCallback((rows) => {
    return rows.map((row) => {
      return {
        studentName: row[" /"],
        gender: row[""],
        supervisorName: row[" /"],
        level: row[""],
        groupNumber: row[""],
        joinYear: row[" "],
        joinSemester: row[" "],
        joinMonth: row[" "],
        phoneNumber: formatPhoneNumber(cleanPhoneNumber(row[" "])),
      };
    });
  }, []);

  useEffect(() => {
    if (file) {
      Papa.parse(file, {
        header: true, // Set to true if the first row contains headers
        dynamicTyping: (field) => {
          if (field === " ") {
            return false;
          }

          return true;
        },
        complete: async function (results) {
          console.log(results.data); // Parsed data
          const errors = validateData(results.data);
          if (errors.length > 0) {
            setValidationErrorMessages(errors);
            setError(true);
          } else {
            const token = await getAccessTokenSilently({});
            const students = mapRowsToStudents(results.data);
            try {
              await createStudents(token, students);
              setSubmittedSuccessfully(true);
            } catch (error) {
              console.error(error);
              setError(true);

              if (
                typeof error.response === "object" &&
                error.response &&
                error.response.data &&
                error.response.data.validationErrors
              ) {
                setValidationErrorMessages(
                  error?.response?.data?.validationErrors ?? []
                );
              } else {
                setValidationErrorMessages(["   "]);
              }
            }
          }

          setLoading(false);
        },
        error: function (error) {
          console.error(error.message);
          setError(true);
          setLoading(false);
        },
      });

      if (fileInput.current) {
        fileInput.current.value = "";
      }
    }
  }, [file, validateData, getAccessTokenSilently, mapRowsToStudents]);

  const handleFileUpload = useCallback((event) => {
    setLoading(true);
    setError(false);
    setValidationErrorMessages([]);
    const files = event.target.files; // Get the file
    if (files.length === 0) return; // If no file is selected, return
    setFile(files[0]);
  }, []);

  useEffect(() => {
    if (submittedSuccessfully) {
      setTimeout(() => {
        setSubmittedSuccessfully(false);
      }, 3000);
    }
  }, [submittedSuccessfully]);

  if (submittedSuccessfully) {
    return <Alert severity="success">   </Alert>;
  }

  return (
    <Stack spacing={1}>
      <FileUploadButton
        handleUpload={handleFileUpload}
        loading={loading}
        ref={fileInput}
      />

      <ImportSample />

      <Stack spacing={1} mt={4}>
        {error && (
          <Alert severity="error">
               .        
            
            <Divider sx={{ my: 2 }} />
            {validationErrorMessages.map((message, index) => (
              <Box key={index}>{message}</Box>
            ))}
          </Alert>
        )}
      </Stack>
    </Stack>
  );
}
